@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  cursor: none;
  scroll-behavior: smooth;
  overflow-x: hidden !important;
}

body {
  width: 100%;
  height: 100%;
  overflow-x: hidden !important;
  background: #000000;
  font-family: "Inter", sans-serif;
  color: #ffffff;
  cursor: none;
  scroll-behavior: smooth;
}

li {
  list-style-type: none;
}
a {
  text-decoration: none;
}

@font-face {
  src: url(/static/media/EuclidCircularA-Bold.b4788f2f.ttf);
  font-family: "Euclid-bold";
  font-weight: 700;
}

@font-face {
  src: url(/static/media/EuclidCircularA-Light.29f6c14f.ttf);
  font-family: "Euclid-light";
  font-weight: 300;
}
@font-face {
  src: url(/static/media/EuclidCircularA-Italic.8a03b158.ttf);
  font-family: "Euclid-italic";
  font-weight: 300;
}
@font-face {
  src: url(/static/media/EuclidCircularA-Medium.a5befbfc.ttf);
  font-family: "Euclid-medium";
  font-weight: 500;
}
@font-face {
  src: url(/static/media/EuclidCircularA-Regular.d5460088.ttf);
  font-family: "Euclid-regular";
  font-weight: 400;
}

.active-nav {
  width: 100%;
  height: 0px;
  border-bottom: 3px solid #eaae1e;
  /* Vector */
}

